import {customElement, BaseElement, closestElement, isSlotEmpty} from '@here/hds-base';
import {IconStyle} from '@here/hds-icon';
import {IconCategory} from '@here/hds-iconlibrary';
import {ProgressIndicatorColor} from '@here/hds-progress-indicator';
import {html, LitElement, nothing, PropertyValues} from 'lit';
import {property, query, queryAssignedNodes} from 'lit/decorators.js';
import {classMap} from 'lit/directives/class-map.js';
import {ifDefined} from 'lit/directives/if-defined.js';
import {styleMap} from 'lit/directives/style-map.js';
import {styles} from './hds-button.css.js';

export type ButtonType = 'button' | 'submit' | 'reset';
export type ButtonVariant =
  | 'primary'
  | 'secondary'
  | 'subtle'
  | 'success'
  | 'warning'
  | 'danger'
  | 'link'
  | 'danger-quiet'
  | 'success-quiet'
  | 'warning-quiet'
  | 'subtle-quiet'
  | 'floating'
  | 'on-color-primary'
  | 'on-color-secondary';

export type ButtonSize = 'extra-small' | 'small' | 'medium' | 'large';
const spinnerVariantColor: {[V in ButtonVariant]: ProgressIndicatorColor} = {
  primary: 'on-light',
  secondary: 'on-light',
  subtle: 'on-light',
  danger: 'on-dark',
  success: 'on-dark',
  warning: 'on-light',
  'danger-quiet': 'on-dark',
  'success-quiet': 'on-dark',
  'warning-quiet': 'on-light',
  link: 'action',
  'subtle-quiet': 'on-light-subtle',
  floating: 'on-light',
  'on-color-primary': 'on-light',
  'on-color-secondary': 'on-dark',
};

/**
 * @slot default Label for the button
 */
@customElement('hds-button')
export class Button extends BaseElement {
  static styles = styles;
  static shadowRootOptions = {...LitElement.shadowRootOptions, delegatesFocus: true};
  @property({type: String, reflect: true}) variant: ButtonVariant = 'primary';
  @property({type: String, reflect: true}) size: ButtonSize = 'medium';
  @property({type: Boolean, reflect: true}) disabled = false;
  @property({type: String}) width;
  @property({type: String}) icon;
  @property({type: String, attribute: 'icon-style'}) iconStyle: IconStyle = 'solid';
  @property({type: String, attribute: 'icon-category'}) iconCategory: IconCategory = 'core-ui';
  @property({type: String, attribute: 'icon-right'}) iconRight = '';
  @property({type: String, attribute: 'icon-right-category'})
  iconRightCategory: IconCategory = 'core-ui';
  @property({type: String, attribute: 'icon-right-style'}) iconRightStyle: IconStyle = 'solid';
  @property({type: String, attribute: 'aria-label'}) ariaLabel;
  @property({type: Boolean, reflect: true}) loading = false;
  @property({type: String, reflect: true}) type: ButtonType = 'button';
  @query('#Button') buttonElement!: HTMLButtonElement;
  @query('slot') labelSlot!: HTMLSlotElement;
  @queryAssignedNodes('', true) listLabelItems!: Array<Node>;
  protected iconOnly = false;
  protected isTabbable = true;

  focus() {
    return this.buttonElement && this.buttonElement.focus();
  }

  blur() {
    return this.buttonElement && this.buttonElement.blur();
  }

  click() {
    if (!this.isButtonDisabled()) {
      this.buttonElement.click();
    }
  }

  protected update(_changedProperties: PropertyValues) {
    super.update(_changedProperties);
    if (
      !_changedProperties.has('iconOnly') &&
      (_changedProperties.has('icon') ||
        _changedProperties.has('icon-style') ||
        _changedProperties.has('icon-category'))
    ) {
      this.iconOnly = this.icon && isSlotEmpty(this.listLabelItems);
      this.requestUpdate();
    }
  }

  protected render() {
    const _disabled: boolean = this.isButtonDisabled();
    const _ariaLabel: string | undefined = this.getAriaLabel();
    const classes = {
      'hds-button--icon': !!this.icon,
      'hds-button--icon-right': !!this.iconRight,
      'hds-button--icononly': this.iconOnly,
      'hds-button--disabled': _disabled,
    };
    classes[`hds-button--variant-${this.variant}`] = true;
    classes[`hds-button--size-${this.size}`] = true;
    if (this.variant !== 'link') classes['hds-button--loading'] = this.loading;

    const labelStyles = {
      visibility: this.loading && this.variant !== 'link' ? 'hidden' : undefined,
    };

    const localStyles = {
      width: this.width,
    };

    return html`
      <button
        id="Button"
        @focus="${this.focus}"
        @blur="${this.blur}"
        class="hds-button ${classMap(classes)}"
        style="${styleMap(localStyles)}"
        ?disabled="${_disabled}"
        type="${this.type}"
        @click="${this.handleAction}"
        aria-label="${ifDefined(_ariaLabel)}"
      >
        ${this.loading && this.variant !== 'link' ? this.renderSpinner() : nothing}
        <span class="hds-button-label-container" style=${styleMap(labelStyles)}>
          ${this.renderIcon(this.icon, this.iconCategory, this.iconStyle)}
          <span class="hds-button-label">
            <slot></slot>
          </span>
          ${this.renderIcon(this.iconRight, this.iconRightCategory, this.iconRightStyle)}
        </span>
      </button>
    `;
  }

  private renderIcon(icon, category, style) {
    if (!icon || icon.trim() === '') return nothing;

    let iconSize = '16px';
    if (this.iconOnly) {
      if (this.size === 'extra-small') {
        iconSize = '8px';
      } else if (this.size === 'large') {
        iconSize = '24px';
      }
    }
    return html` <hds-icon
      name="${icon}"
      size="${iconSize}"
      icon-style="${style}"
      category="${category}"
    ></hds-icon>`;
  }

  private renderSpinner() {
    const color = spinnerVariantColor[this.variant];
    return html`
      <span class="hds-button-loading-container">
        <hds-progress-indicator
          size="small"
          color="${color}"
          indicator-type="circular"
        ></hds-progress-indicator>
      </span>
    `;
  }

  private isButtonDisabled() {
    if (this.disabled) return true;
    if (this.variant !== 'link') return this.loading;
    return false;
  }

  private getAriaLabel() {
    if (!this.ariaLabel || this.ariaLabel === '') {
      if (this.loading) {
        return 'Loading';
      }
      return undefined;
    }
    return this.ariaLabel;
  }

  private handleAction(e): void {
    e.stopPropagation();
    e.preventDefault();
    if (this.isButtonDisabled()) {
      return;
    }
    this.updateComplete;
    if (this.type === 'submit' || this.type === 'reset') {
      const form = closestElement<HTMLFormElement>('FORM', this);
      if (form) {
        if (this.type === 'submit') {
          form.requestSubmit();
        } else {
          // form.dispatchEvent(new Event('reset'));
          form.reset();
        }
      }
    }
    this.dispatchEvent(
      new MouseEvent('click', {
        bubbles: true,
        composed: true,
      })
    );
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-button': Button;
  }
}
